* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  text-transform: capitalize;
  background-color: rgb(238, 238, 224);
}

.mainContainer {
  height: 100vh;

  display: flex;
  flex-direction: column;
  justify-content: center;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.container p {
  font-size: 30px;
  margin-top: 20px;
  color: red;
}
